	module.exports = {"data":{"markdownRemark":{"html":"<p>Kid Klock for iOS is an app that indicates to a child whether they should stay in bed and go back to sleep or if they can get up.</p>\n<p>Kid Klock was feature-complete for its MVP but was blocked on needing artwork.\nI started working on iCloud syncing but had no way to test it.\nAdditionally I did not feel I had the time available to support an app that other people might actually use.\nI stopped working on Kid Klock due to these concerns.</p>\n<p>The alarm uses two states, awake and asleep, in addition to cartoony animal imagery to indicate to the child the time.\nFor instance, if the image is in the asleep state, the child remembers that they should also be asleep.</p>\n<p>Because of its nature as an app, this will necessitate an iOS device be left within view of the child's bed.</p>","fields":{"project":{"title":"Kid Klock for iOS","subtitle":"Indicate time to a toddler","status":"retired","createdAtTimestamp":"2012-05-13T03:09Z","createdAt":"May 2012","deployedUrl":null,"endedAtTimestamp":"2012-10-07T15:21:41.000Z","endedAt":"October 2012","sourceUrl":"https://github.com/adamstegman/Kid-Klock"}}},"site":{"siteMetadata":{"title":"Adam Stegman"}}},"pathContext":{"slug":"/projects/kid-klock"}}

